import React from "react";
import Card from "../card/Card";
import {
  faReact,
  faHtml5,
  faCss3Alt,
  faJava,
  faGitAlt,
  faGithub,
  faBootstrap,
} from "@fortawesome/free-brands-svg-icons";
import "./Skills.css";
import { faDatabase } from "@fortawesome/free-solid-svg-icons";

const Skills = () => {
  return (
    <div className="skills-container">
      <div className="container">
        <div
          style={{
            background: "red",
            height: "3px",
            width: "45px",
            marginBottom: "15px",
          }}
        ></div>
        <h2>TECHNOLOGIES</h2>
        <div className="row ">
          <div
            className="col-sm skills-card "
            data-toggle="tooltip"
            data-placement="top"
            title="Html5"
            style={{ color: "#e96228" }}
          >
            <Card icon={faHtml5} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="Css3"
            style={{ color: "#026CB5" }}
          >
            <Card icon={faCss3Alt} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="Bootstrap"
            style={{ color: "#6C42A0" }}
          >
            <Card icon={faBootstrap} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="React Hooks"
            style={{ color: "#5ED3F3" }}
          >
            <Card icon={faReact} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="Java"
            style={{ color: "#E1312D" }}
          >
            <Card icon={faJava} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="Git"
            style={{ color: "#EA4C27" }}
          >
            <Card icon={faGitAlt} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="GitHub"
            style={{ color: "#171515" }}
          >
            <Card icon={faGithub} />
          </div>
          <div
            className="col-sm skills-card"
            data-toggle="tooltip"
            data-placement="top"
            title="Mysql"
            style={{ color: "#1F4CC9" }}
          >
            <Card icon={faDatabase} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Skills;
