import React from "react";
import "./ContactMe.css";
import {
  faFacebookSquare,
  faGithubSquare,
  faInstagramSquare,
  faLinkedin,
} from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelopeSquare } from "@fortawesome/free-solid-svg-icons";

const ContactMe = () => {
  return (
    <div className="contactme-container">
      <div className="row">
        <div className="col-sm-5 contactme-container-photo">
          <div className="contactme-photo" alt="my-avatar"></div>
        </div>
        <div className=" col-sm-7 contactme-content-information">
          <div className="contactme-information">
            <div
              style={{
                background: "red",
                height: "3px",
                width: "45px",
                marginBottom: "15px",
              }}
            ></div>
            <h2>SOCIAL NETWORK</h2> <div></div>
            <div className="row contactme-row-icon">
              <div className="col-sm contactme-icon">
                <a
                  href="https://github.com/AndreeDev1997"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon
                    icon={faGithubSquare}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="AndreeDev1997"
                  />
                </a>
              </div>
              <div className="col-sm contactme-icon">
                <a
                  href="https://www.linkedin.com/in/richard-andree-ramos-benites-b841271a8/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon
                    icon={faLinkedin}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="richard-andree-ramos-benites-b841271a8"
                  />
                </a>
              </div>{" "}
              <div className="col-sm contactme-icon">
                <a
                  href="https://accounts.google.com/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon
                    icon={faEnvelopeSquare}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="andreerb.2020@gmail.com"
                  />
                </a>
              </div>{" "}
              <div className="col-sm contactme-icon">
                <a
                  href="https://www.instagram.com/andreerb2020"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon
                    icon={faInstagramSquare}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="andreerb2020"
                  />
                </a>
              </div>
              <div className="col-sm contactme-icon">
                <a
                  href=" https://www.facebook.com/richard.ramos.3367"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <FontAwesomeIcon
                    icon={faFacebookSquare}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="richard.ramos.3367"
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactMe;
