import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import "./Card.css";

const Card = ({ icon }) => {
  return (
    <div className="card card-container">
      <div className="card-body">
        <FontAwesomeIcon icon={icon} style={{ fontSize: "3em" }} />
      </div>
    </div>
  );
};

export default Card;
